<template>
  <v-flex >
    <v-text-field
      ref="search"
      v-model="search"
      :loading="isLoading"
      :search-input.sync="search"
      label="Â∞ãÊâæÊ≠£Âú®Êè™Âúò..."
      append-icon="search"
      clear-icon="mdi-close-circle"
      clearable
      solo
      @click:clear="this.fetchGroupList"
    ></v-text-field>
    <!-- ÊêúÂ∞ãbarÁµêÊùü -->

    <!-- Âç°ÁâáÈñãÂßã -->

    <v-layout py-3 px-2 column v-for="(item,index) in items" :key="index">
      <v-hover>
        <v-card
          @click="goToGroupPage(items[index]._id)"
          slot-scope="{ hover }"
          :class="`elevation-${hover ? 8 : 2}`"
          style="cursor: pointer; border-radius:15px "
        >
          <v-expand-transition leave-absolute="true">
            <div
              v-if="hover"
              class="d-flex transition-fast-in-fast-out primary darken-2 v-groupCard--reveal white--text"
            >
              üëâÊ∫ñÂÇôÈÅäÁé© :
              <v-flex xs2 sm2 md1 shrink v-for="game in items[index].game" :key="game">
                <v-chip color="primary" text-color="white">{{game}}</v-chip>
              </v-flex>
            </div>
          </v-expand-transition>
          <v-card-actions>
            <v-layout row wrap>
              <v-flex pa-2>
                <v-flex
                  class="grey--text"
                  style="font-size:16px"
                >{{items[index].city}} {{items[index].dist}} {{items[index].address}}</v-flex>

                <v-flex>
                  <v-flex md12 style="font-size:1.8rem;">
                    <span>{{items[index].name}}</span>
                    

                    <v-flex xs12 style="float:right;">
                      <v-chip
                        v-if="items[index].now < items[index].max"
                        label
                        outline
                        selected
                        color="primary"
                      >
                        <span
                          style=" font-size:16px;"
                        >ÁõÆÂâç‰∫∫Êï∏: {{items[index].now}} / {{items[index].max}} ‰∫∫</span>

                        <v-icon right>mdi-account</v-icon>
                      </v-chip>
                      <v-chip v-else label outline selected color="red">
                        <span
                          style="font-size:16px;"
                        >ÁõÆÂâç‰∫∫Êï∏: {{items[index].now}} / {{items[index].max}} ‰∫∫</span>

                        <v-icon right>mdi-account</v-icon>
                      </v-chip>
                    </v-flex>
                  </v-flex>
                </v-flex>

                <v-flex>
                  <v-chip v-if="items[index].storeName != ''" label outline color="grey darken-3">
                    <v-icon left>mdi-map-marker</v-icon>
                    <span style=" font-size:16px;">Ê°åÈÅäÂ∫ó : {{items[index].storeName}} </span>
                  </v-chip>
                  <v-chip v-if="items[index].storeName == ''" label outline color="grey darken-3">
                    <v-icon left>mdi-map-marker</v-icon>
                    <span style=" font-size:16px;">Ê°åÈÅäÂ∫ó : Ëá™Ë®Ç </span>
                  </v-chip>
                    <v-chip style="color:white;" v-if="items[index].apply" label outline color="orange darken-2">
                    <span style=" font-size:16px;">üßê&nbsp;&nbsp;&nbsp;Áî≥Ë´ãÂä†ÂÖ•</span>
                  </v-chip>
                    <v-chip style="color:white;" v-if="!items[index].apply" label outline color="green darken-2">
                    <span style=" font-size:16px;">ü§ó&nbsp;&nbsp;&nbsp;Ëá™Áî±Âä†ÂÖ•</span>
                  </v-chip>
                  <v-chip style="color:white;" v-if="items[index].memberId == memberId" label  color="red">
                      <span style=" font-size:16px;">üí™&nbsp;&nbsp;&nbsp;ÊàëÊòØ‰∏ªÊè™</span>
                    </v-chip>
                </v-flex>
                
                <v-flex xs12>
                  <v-chip label outline color="grey darken-3">
                    <v-icon left>mdi-calendar</v-icon>
                    <span style=" font-size:16px;">Êó•Êúü: {{items[index].date}}</span>
                  </v-chip>
                  <v-chip label outline color="grey darken-3">
                    <v-icon left>mdi-clock</v-icon>
                    <span style=" font-size:16px;">ÈñãÂßãÊôÇÈñì : {{items[index].start}}</span>
                  </v-chip>
                  <v-chip label outline color="grey darken-3">
                    <span style=" font-size:16px;">ÁµêÊùüÊôÇÈñì : {{items[index].end}}</span>
                  </v-chip>
                  <v-chip style="color:white;" v-if="items[index].novice" label color="blue">
                    <span style=" font-size:16px;">üëç&nbsp;&nbsp;&nbsp;Êñ∞ÊâãÂèãÂñÑ</span>
                  </v-chip>
                  
                
                </v-flex>
              </v-flex>
            </v-layout>
          </v-card-actions>
        </v-card>
      </v-hover>
    </v-layout>
    <!-- Âç°ÁâáÁµêÊùü -->

    <!-- ÂàÜÈ†ÅËôïÁêÜ -->
    <template>
      <v-flex>
        <div class="text-xs-center">
          <v-pagination v-if="!isSearching && items.length != 0" v-model="page" :length="pages"></v-pagination>
        </div>
        <div class="text-xs-center">
          <v-pagination
            v-if="isSearching && items.length != 0"
            v-model="searchPage"
            :length="searchPages"
          ></v-pagination>
        </div>
        <div>
          <v-alert
            v-if="items.length == 0"
            :value="true"
            color="primary"
            icon="priority_high"
            outline
          >Ê≤íÊúâ‰ªª‰ΩïÊè™Âúò</v-alert>
        </div>
      </v-flex>
      <v-flex></v-flex>
    </template>
  </v-flex>
</template>
          



<script>
import { apiGetGroupList, apiSearchGroup } from "../../utils/api.js";

export default {
  props: {
    // groups: Object
  },
  data() {
    return {
      search: null,
      isLoading: false,
      isSearching: false,
      errMsg: "",
      groupId:'',
      page: 1,
      pages: 1,
      searchPage: 1,
      searchPages: 1,
      items: [],
      memberId:'',
    };
  },
  inject: ["reload"],
  created() {
    this.$data.memberId = this.$store.state.memberId;
    this.$bus.$emit("isLoading:push", true);
    this.fetchGroupList();
  },
  methods: {
    goToGroupPage(id) {
      this.$router.push(`/group/${id}`);
    },
    //ÂæóÂà∞Ê∏ÖÂñÆÈñãÂßã
    fetchGroupList() {
      this.isLoading = true;
      this.$data.isSearching = false;

      apiGetGroupList(
        {
          page: this.$data.page,
          isPlaying: false
        },
      )
        .then(res => {
          //Êé•Êî∂Â§±ÊïóÔºåËº∏ÂÖ•ÈåØË™§
          if (res.data.status === 400) {
            this.$data.errMsg = res.data.msg;
            //ÊêúÂ∞ãÂ§±ÊïóË∑≥Âá∫ÈåØË™§Ë®äÊÅØ

            console.log(this.$data.errMsg);
            this.$bus.$emit("message:push", this.$data.errMsg, "error");
            this.$bus.$emit("isLoading:push", false);
            return;
          }
          //ÂæóÂà∞Êé•Êî∂ÁµêÊûú
          console.log("fetch result", res.data);
          this.$data.items = res.data.group.docs;
          this.$data.pages = res.data.group.pages;
          this.$data.page = res.data.group.page;
          this.$bus.$emit("isLoading:push", false);
          // this.reload();
          this.$data.isSearching = false;
        })
        .catch(err => {
          //Á∑®ËºØÂ§±ÊïóÔºå‰º∫ÊúçÂô®ÈåØË™§
          console.log(err);
          this.$bus.$emit("message:push", err, "error");
          this.$bus.$emit("isLoading:push", false);
        })
        .finally(() => (this.isLoading = false));
    },
    //ÂæóÂà∞Ê∏ÖÂñÆÁµêÊùü

    //ÊêúÂ∞ãÂäüËÉΩÈñãÂßã
    searchGroupList() {
      if (this.search == null) {
        return;
      }
      if (this.search == "") {
        this.fetchGroupList();
        return;
      }
      this.isSearching = true;
      this.isLoading = true;
      console.log();

      apiSearchGroup(
        {
          str: this.search,
          page: this.searchPage,
          isPlaying: false
        },
      )
        .then(res => {
          //ÊêúÂ∞ãÂ§±ÊïóÔºåËº∏ÂÖ•ÈåØË™§
          if (res.data.status === 400) {
            this.$data.errMsg = res.data.msg;
            //ÊêúÂ∞ãÂ§±ÊïóË∑≥Âá∫ÈåØË™§Ë®äÊÅØ
            console.log(this.$data.errMsg);
            this.$bus.$emit("message:push", this.$data.errMsg, "error");
            this.$bus.$emit("isLoading:push", false);
            return;
          }
          //ÂæóÂà∞ÊêúÂ∞ãÁµêÊûú
          console.log(res.data);
          this.$data.items = res.data.group.docs;
          this.$data.searchPages = res.data.group.pages;
          this.$data.searchPage = res.data.group.page;
        })
        .catch(err => {
          //Á∑®ËºØÂ§±ÊïóÔºå‰º∫ÊúçÂô®ÈåØË™§
          console.log(err);
          this.$bus.$emit("message:push", err, "error");
        })
        .finally(() => (this.isLoading = false));
    }
  },
  watch: {
    search(val) {
      this.searchGroupList();
    },
    page(val) {
      this.$vuetify.goTo(this.$refs.search)
      this.fetchGroupList();
    },
    searchPage(val) {
      this.$vuetify.goTo(this.$refs.search)
      this.searchGroupList();
    }
  }
};
</script>
<style>
.v-groupCard--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  width: 100%;
  height: 5rem;
  font-size: 1.5rem;
}
</style>
